.sections {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.split {
    padding: 0.5rem;
    background-color: white;
    box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
    border-radius: 5px;
    flex: auto;
}

.about-me {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 0.5rem;
    background-color: white;
    box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
    border-radius: 5px;
}

.about-me-title {
    margin: 0;
}

.email {
    color: #a3b18a; 
}

