.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.login-item {
    display: flex;
}

.warning {
    margin-bottom: 0.5rem;
    background-color: #FFCCCC;
    border-radius: 5px;
    border: 1px solid brown;
    padding: 0.5rem;
}

.warning-icon {
    padding-left: 0.75rem;
    font-size: 20px;
}