.project-container {
    display: flex;
}

.project-cards {
    display: flex;
    /* only do this on smaller screen sizes */
    flex-wrap: wrap;
    justify-content: center;
    /* margin-left: 0.5rem; */
    /* margin-right: 140px; */
}

.error {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 620px) {
    .project-container {
        flex-direction: column;
    }

    .project-cards{
        max-width: 280px;
    }
}