.container {
    /* margin: 0.5rem; */
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    background-color: white;
    border-radius: 3px;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
    width: 250px;
    max-height: 300px;
    font-size: 22px;
}

.title{
    font-size: 24px;
    font-weight: 300;
    margin: auto;
    padding: 20px 0 20px;
}

.label {
    margin-left: 17px;
    padding: 0.25rem;
}

.input {
    border: 1px solid;
    background-color: whitesmoke;
    border-color: #a3b18a;
    border-radius: 3px;
    height: 20px;
    margin-left: 20px;
    margin-right: 20px;
}

.submit-button {
    color: white;
    border: #a3b18a;
    border-color: #a3b18a;
    background-color: #a3b18a;
    border-radius: 3px;
    outline: none;
    padding: 10px;
    width: 80px;
    margin: auto;
    margin-top: 20px;
}

.submit-button:hover {
    cursor: pointer;
    /* background-color: whitesmoke; */
}

.bottom-text {
    font-size: 14px;
    font-weight: 400;
    padding: 20px 0 20px;
    margin: auto;
}

