.navbar {
    margin: 0;
    padding: 0.25rem;
    list-style-type: none;
    overflow: hidden;
    background-color: white;
    box-shadow: 0px 3px 5px rgba(0,0,0,0.2);
}

.first {
    margin-left: 10%;
}

@media screen and (max-width: 452px) {
    .first {
        margin-left: 0px;
    }
}

.left {
    float: left;
}

.right {
    float: right;
}

.sign-up {
    margin: 0;
    border: 1px solid black;
    border-radius: 5px;
    border-color: white;
    background-color: #a3b18a;
    color: white !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
}

.item-name {
    display: block;
    color: black;
    text-align: center;
    padding: .75rem;
    text-decoration: none;
    font-size: 18px;
    font-weight: 300;
}

.active {
    border-bottom: 2px solid #a3b18a;
}

span:hover {
    cursor: pointer;
}